import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {environment} from '../environments/environment';
import {AngularFireModule} from 'angularfire2';
import {AngularFirestoreModule} from 'angularfire2/firestore';
import { AppComponent } from './app.component';
import {AppService} from './app.service';
import { CountriesComponent } from './countries/countries.component';
import {RouterModule, Routes} from '@angular/router';
import { CurrencyComponent } from './currency/currency.component';
import { ImportMonthlyComponent } from './import-monthly/import-monthly.component';
import { JuneusdComponent } from './juneusd/juneusd.component';
import {Ng2SmartTableModule} from 'ng2-smart-table';
import { TableComponent } from './table/table.component';

export const routes: Routes = [
  {path: '', component: TableComponent},
  {path: 'table', component: TableComponent},
  {path: 'countries', component: CountriesComponent},
  {path: 'importmonthly', component: ImportMonthlyComponent},
  {path: 'currency', component: CurrencyComponent},
  {path: 'june', component: JuneusdComponent},
  // {
  //   path: 'checkout',
  //   loadChildren: '../../app/pages/checkout/modules/checkout.module#CheckoutModule',
  //   // canActivate: [PublicGuard]
  // }
];

@NgModule({
  declarations: [
    AppComponent,
    CountriesComponent,
    CurrencyComponent,
    ImportMonthlyComponent,
    JuneusdComponent,
    TableComponent
  ],
  imports: [
    BrowserModule,
    AngularFireModule.initializeApp(environment.firebase, 'angularfs'),
    AngularFirestoreModule,
    RouterModule.forRoot(routes),
    Ng2SmartTableModule

  ],
  exports: [RouterModule],
  providers: [AppService],
  bootstrap: [AppComponent]
})


export class AppModule { }
